	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Project Overview","depth":2},{"value":"Target Audience","depth":2},{"value":"Storytelling Strategy","depth":2},{"value":"Why (Anti)socialized Matters","depth":2}],"html":"<h2>Project Overview</h2>\n<p>(Anti)socialized is a short film that uses visual metaphor and storytelling to explore how gendered socialization impacts our sexual interactions. The film examines popular notions of masculinity and femininity, and proposes ways in which we can challenge these norms and transform our interactions with each other.</p>\n<h2>Target Audience</h2>\n<p>This film is designed for individuals of all genders and sexual orientations, but it focuses specifically on the sexual interactions between male and female identifying individuals. The Government of Ontario estimates that 1 in 3 Canadian women will be sexually assaulted in their lifetime, and 99% of the time, the perpetrator is male. (Anti)socialized aims to dig deep into the question of why problematic sexual encounters seem to be so common in our society, and it does so by providing validation and compassion to both male and female identifying individuals.</p>\n<h2>Storytelling Strategy</h2>\n<p>This project is inspired by my own experiences of feeling sexually violated. Initially, I was going to approach this project by telling my own story, but I eventually realized that if my goal was to reach both men and women, I could do so more effectively by utilizing and applying what I’ve learned through my experiences to tell a common, accessible, and nuanced story, that doesn’t need to center around my specific encounters.</p>\n<p>There are 5 sections to the film:</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 88.63123993558777%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Five different parts of the short film\"\n        title=\"\"\n        src=\"/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-bc86b.png\"\n        srcset=\"/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-c3a8a.png 125w,\n/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-54de6.png 250w,\n/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-bc86b.png 500w,\n/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-da458.png 750w,\n/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-b8502.png 1000w,\n/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-34ab4.png 1500w,\n/the-next-website/static/movie-sections-04-dae8a6e233b7d20fd1386823e9ab58bf-268e7.png 3105w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Each section builds on the metaphors and narrative provided in the previous sections, and bright, colourful, playful imagery helps to balance out the somewhat controversial, taboo, and polarizing subject matter. Both male and female narrators are utilized in order to provide a sense of fairness, validation, and compassion.</p>\n<h2>Why (Anti)socialized Matters</h2>\n<p>Recently, there has been an overwhelming amount of heated dialogue about sexual consent, violence, and harassment. Most of the conversations happening around these issues are fuelled by anger and defensiveness. Although these are valid emotions and are important for individuals to express, they are not tools that lead to understanding, change, or compassion. Anti(socialized) looks at social norms that result in problematic behaviour, and instead of blaming the individual, it looks at root causes that we are all subjected to by society. Utilizing a non-confrontational approach, this film gently urges viewers to look at their own socialization in a new, more compassionate way that hopefully results in behavioural change.</p>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <iframe src=\"https://www.youtube.com/embed/CMDOAmtsEcM?rel=0&amp;showinfo=0\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n          </div>\n          </div>","frontmatter":{"title":"(Anti)socialized","graduate":"Danielle Sheahan","category":"Time Based Communication","category2":"Motion Design","category3":"Communication Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5,"src":"/the-next-website/static/cactus0-855c134552a726b61c66f1c4d6622d66-0f681.png","srcSet":"/the-next-website/static/cactus0-855c134552a726b61c66f1c4d6622d66-c1674.png 375w,\n/the-next-website/static/cactus0-855c134552a726b61c66f1c4d6622d66-34f47.png 750w,\n/the-next-website/static/cactus0-855c134552a726b61c66f1c4d6622d66-0f681.png 1500w,\n/the-next-website/static/cactus0-855c134552a726b61c66f1c4d6622d66-5544e.png 1920w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/anti-socialized/","name":"/(Anti)socialized/"}}

